import React from "react";
import { Route, Switch } from "react-router-dom";
import Home from "./containers/Home";
import NotFound from "./containers/NotFound";
import Login from "./containers/Login";
import MainSearch from "./containers/MainSearch";
import MainSearchResults from "./containers/PatientSearchResults";
import MainTab from "./components/TabView";
import AppliedRoute from "./components/AppliedRoute";

export default ({ childProps }) =>
  <Switch>
    <AppliedRoute path="/" exact component={Home} props={childProps} />
    <AppliedRoute path="/login" exact component={Login} props={childProps} />
    <AppliedRoute path="/mainsearch" exact component={MainSearch} props={childProps} />
    <AppliedRoute path="/patientsearchresults" exact component={MainSearchResults} props={childProps} />
    <AppliedRoute path="/maintabview" exact component={MainTab} props={childProps} />
    { /* Finally, catch all unmatched routes */ }
    <Route component={NotFound} />
  </Switch>;

