import React, { Component } from 'react'
import {PanelGroup, Panel} from 'react-bootstrap';


export default class ContactDetailsTab extends Component{

    getResidentialAddress = (pbase,type) => {
        var basedetails = this.props.details;

        if(basedetails === undefined){
            return null
        }else if (type==='patient'){
            return (

                <div className="addrdiv">
                    <dl className="dl-horizontal">
                                <dt>Line 1:</dt>
                                <dd>{basedetails.ResidentialAddress.Line1}</dd>

                                <dt>Line 2:</dt>
                                <dd>{basedetails.ResidentialAddress.Line2}</dd>

                                <dt>Line 3:</dt>
                                <dd>{basedetails.ResidentialAddress.Line3}</dd>

                                <dt>Line 4:</dt>
                                <dd>{basedetails.ResidentialAddress.Line4}</dd>

                                <dt>Postal Code:</dt>
                                <dd>{basedetails.ResidentialAddress.PostalCode}</dd>
                    </dl>
                </div>
            );
        }else{
            return (


                <div className="addrdiv">
                    <dl className="dl-horizontal">
                                <dt>Name:</dt>
                                <dd>{basedetails.NextOfKin.FirstName}</dd>

                                <dt>Relationship:</dt>
                                <dd>{basedetails.NextOfKin.Relationship}</dd>

                                <dt>Telephone:</dt>
                                <dd>{basedetails.NextOfKin.ContactDetails.TelephoneNumber}</dd>
                    </dl>

                    <Panel>
                        <Panel.Heading>
                        <Panel.Title componentClass="h3">Residential Address</Panel.Title>
                        </Panel.Heading>
                        <Panel.Body>
                                <address>
                                {basedetails.NextOfKin.ContactDetails.ResidentialAddress.Line1 === null ? null :  basedetails.NextOfKin.ContactDetails.ResidentialAddress.Line1} <br/>
                                {basedetails.NextOfKin.ContactDetails.ResidentialAddress.Line2 === null ? null :  basedetails.NextOfKin.ContactDetails.ResidentialAddress.Line2} <br/>
                                {basedetails.NextOfKin.ContactDetails.ResidentialAddress.Line3 === null ? null :  basedetails.NextOfKin.ContactDetails.ResidentialAddress.Line3} <br/>
                                {basedetails.NextOfKin.ContactDetails.ResidentialAddress.Line4 === null ? null :  basedetails.NextOfKin.ContactDetails.ResidentialAddress.Line4}
                                </address>
                        </Panel.Body>
                    </Panel>
                    

                </div>

                
            );
        }

    }

    getPatientNextOfKinName = () => {

    }

    render(){
        const addr = this.getResidentialAddress(this.props.details, 'patient');
        const NKaddr = this.getResidentialAddress(this.props.details, 'nok');
        return(
         <div className="contactdetails">
             <PanelGroup accordion id="accordion">
                <Panel eventKey="1">
                    <Panel.Heading>
                    <Panel.Title toggle>Residential Address</Panel.Title>
                    </Panel.Heading>
                    <Panel.Body collapsible>
                                        {addr}    
                    </Panel.Body>
                </Panel>
                <Panel eventKey="2">
                    <Panel.Heading>
                    <Panel.Title toggle>Next of Kin</Panel.Title>
                    </Panel.Heading>
                    <Panel.Body collapsible>
                        {NKaddr}
                    </Panel.Body>
                </Panel>
                </PanelGroup>
         </div>
        );
    }
}